$button-disabled-font-color: rgba(0, 0, 0, 0.3);
$button-disabled-bg-color: #f7f7f7;

$button-bg-color: #f8f8f8;
$button-active-bg-color: #dedede;

.btn {
    width:100%;
    height:100%;
    display: block;
    position: absolute;
    font-family: $FontDefault;
    text-align: center;
    text-decoration: none;
    color:inherit;
    background-color: inherit;
    line-height: unit($button-height/$button-font-size);
    //overflow: hidden;
    border-width: 0;
    outline: 0;
    appearance: none;
    user-select: none;
    white-space: nowrap;
    border-radius: inherit;
    padding:0;
    &:hover {
        -webkit-tap-highlight-color: rgba(0,0,0,0); 
    }
    &:focus {
        outline: 0;
    }
    &:not(.btn-disabled):active {
        //color: $button-active-font-color;
        transform: scale(0.95);
        
    }
    &.btn-disabled {
        color: $button-disabled-font-color;
        background-color: $button-disabled-bg-color;
    }
    
}
.img-btn {
    position: relative;
    display: block;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
    border-width: 0;
    outline: 0;
    -webkit-appearance: none;
    &:focus {
        outline: 0;
    }

    .img-btn-disabled {
        -webkit-filter: grayscale(100%);
        -moz-filter: grayscale(100%);
        -ms-filter: grayscale(100%);
        -o-filter: grayscale(100%);
        filter: grayscale(100%);
        filter: gray;
    }
    .img-btn-loading {
        background: rgba(255, 255, 255, 0.3);
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        position: absolute;
        z-index: 2;
    }
}
.loading {
    width: 20px;
    height: 20px;
    display: inline-block;
    vertical-align: middle;
    animation: loadingAnimation 1s steps(12, end) infinite;
    background: transparent
        url('data:image/svg+xml;base64,')
        no-repeat;
    background-size: 100%;
    &.loading_transparent {
        background-image: url('data:image/svg+xml;base64,');
    }
}

@-webkit-keyframes loadingAnimation {
    0% {
        transform: rotate3d(0, 0, 1, 0deg);
    }

    100% {
        transform: rotate3d(0, 0, 1, 360deg);
    }
}

@keyframes loadingAnimation {
    0% {
        transform: rotate3d(0, 0, 1, 0deg);
    }

    100% {
        transform: rotate3d(0, 0, 1, 360deg);
    }
}
</style>