// 
// div
<template>
    <div class="movie-clip-container" :style="containerStyle">
        <img
            v-for="item in movieClip"
            :key="item.id"
            :src="item.src"
            class="frame"
            :style="{opacity: isFrameShow(item.id)}"
        />
    </div>
</template>

<script>
export default {
    name: 'MovieClip',
    data: function() {
        return {
            containerStyle: 'opacity:0',
            lastAnimationTime: 0,
            frameIndex: 1,
        };
    },
    props: {
        /**
         * srcobjectid
         */
        imageArray: {
            type: Array,
            default: function() {
                return [''];
            },
        },
        /**
         * 
         */
        fps: {
            type: Number,
            default: 24,
        },
        /**
         * 
         */
        startPlay: {
            type: Boolean,
            default: false,
        },
        pause: {
            type: Boolean,
            default: false,
        },
        /**
         * 
         */
        loop: {
            type: Boolean,
            default: false,
        },
        /**
         * 
         */
        reversePlay: {
            type: Boolean,
            default: false,
        },
        /**
         * 
         */
        specFrame: {
            type: Object,
            default: function() {
                return {
                    startIndex: -1,
                    endIndex: -1,
                };
            },
        },
    },
    watch: {
        startPlay: function(val) {
            if (val) {
                this.reset();
                this.play();
            } else {
                this.reset();
            }
        },
    },
    computed: {
        /**
         *  ms
         */
        animationInterval: function() {
            return 1000 / this.fps;
        },
        movieClip: function() {
            let ret = [];
            this.imageArray.reduce((prev, cur) => {
                ret.push({
                    id: ret.length,
                    src: cur,
                });
            }, ret);
            return ret;
        },
    },
    created: function() {},
    methods: {
        isFrameShow: function(index) {
            return index == this.frameIndex ? 1 : 0;
        },
        /**
         * 
         */
        playEnterFrame: function() {
            if (!this.startPlay) {
                return;
            }

            if (this.pause) {
                requestAnimationFrame(this.playEnterFrame);
                return;
            }

            let animationEnd = false; // requestAnimationFrame
            let nowTime = Date.now();

            if (nowTime - this.lastAnimationTime <= this.animationInterval) {
                requestAnimationFrame(this.playEnterFrame);
                return;
            }

            this.lastAnimationTime = nowTime;

            if (this.reversePlay) {
                this.frameIndex--;
            } else {
                this.frameIndex++;
            }

            // 
            // 
            if (
                this.specFrame.startIndex >= 0 &&
                this.specFrame.endIndex >= 0
            ) {
                if (this.reversePlay) {
                    if (this.frameIndex < this.specFrame.startIndex) {
                        this.$emit('frameEnd');
                        if (this.loop) {
                            this.frameIndex = this.specFrame.endIndex;
                        } else {
                            animationEnd = true;
                        }
                    }
                } else {
                    if (this.frameIndex > this.specFrame.endIndex) {
                        this.$emit('frameEnd');
                        if (this.loop) {
                            this.frameIndex = this.specFrame.startIndex;
                        } else {
                            animationEnd = true;
                        }
                    }
                }
            }

            // 
            if (
                this.frameIndex >= this.imageArray.length &&
                !this.reversePlay
            ) {
                this.$emit('frameEnd');
                if (this.loop) {
                    this.frameIndex = 0;
                } else {
                    animationEnd = true;
                }
            }

            if (this.frameIndex < 0 && this.reversePlay) {
                this.$emit('frameEnd');
                if (this.loop) {
                    this.frameIndex = this.imageArray.length - 1;
                } else {
                    animationEnd = true;
                }
            }
            if (!animationEnd) {
                requestAnimationFrame(this.playEnterFrame);
            }
        },
        play: function() {
            this.containerStyle = 'opacity:1';
            this.playEnterFrame();
        },
        reset: function() {
            if (
                this.specFrame.startIndex >= 0 &&
                this.specFrame.endIndex >= 0
            ) {
                this.frameIndex = this.specFrame.startIndex;
            } else {
                if (this.reversePlay) {
                    this.frameIndex = this.imageArray.length - 1;
                } else {
                    this.frameIndex = 0;
                }
            }
        },
    },
};
</script>
<style scoped>
.movie-clip-container {
    position: relative;
}
.frame {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
}
</style>


